@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
    font-family: 'Roboto', sans-serif;
}

.App {
    margin: 20px;
    padding: 40px;
    border: 1px solid black;
}

.App h1 {
    margin-bottom: 40px;
}

.App th {
    border: 1px solid black;
    padding: 5px 0px;
    margin: 0px 10px;
    text-align: center;
}

.App .item {
    border: 1px solid black;
    padding: 5px 60px 5px 8px;
    font-weight: 700;
}

.App .itemid {
    border: 1px solid black;
    padding: 3px 13px;
    text-align: center;
    font-weight: 700;
}

.App .thead {
    background-color: black;
    color: white;

    padding: 5px 15px;
}